@font-face {
  font-family: 'Cakecafe';
  src: url('./Fonts/Cakecafe.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.App {
  text-align: center;
}

.App-body {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; 
  font-size: calc(10px + 2vmin);
  color: white;
  margin-top: 0; 
}

.App-link {
  color: #61dafb;
}
